<template>
  <div class="login-box">
    <el-card class="login-container">
      <el-form :model="loginForm" ref="loginForm" label-width="100" :rules="formRules">
        <el-form-item label="手机号" prop="phone">
          <el-input v-model="loginForm.phone" :placeholder="'input' | solvePlaceholder" />
        </el-form-item>
        <el-form-item label="密码" prop="password">
          <el-input type="password" v-model="loginForm.password" :placeholder="'input' | solvePlaceholder" />
        </el-form-item>
        <el-form-item>
          <el-button type="primary" class="w100 mt15" @click="login('loginForm')">登 陆</el-button>
        </el-form-item>
      </el-form>
    </el-card>
  </div>
</template>
<script>
  import {validateLoginPhone, validateLoginPassword, beforeValidate} from "@/utils/validate"
  import {Apis} from "@/axios"
  import {mapState, mapActions, mapMutations} from 'vuex'
  import {Message} from "element-ui";

  export default {
    name: "Login",
    data() {
      return {
        loginForm: {
          phone:"18111111111",
          password:"123456"
        },
        formRules:{
          phone: [ {required: true, validator: validateLoginPhone, trigger: "blur" } ],
          password: [ {required: true, validator: validateLoginPassword, trigger: "blur"} ]
        }
      }
    },
    computed: {
      ...mapState('user',{
      })
    },
    methods: {
      ...mapActions("user", {
        user_info: '_userInfo'
      }),
      ...mapMutations("user",{
        setToken: 'set_token',
        setIsLogin: 'set_isLogin'
      }),
      login(formRef) {
        let _this = this
        let {login, getInfo} = Apis.member
        if(beforeValidate(_this.$refs[formRef])) {
          login(_this.loginForm).then(res => {
            if (res.data.status == 200) {
              Message.success(res.data.msg)
              _this.saveData(res.data.data)
              _this.$router.push('/dashboard')
              _this.user_info({getInfo})
            } else Message.error(res.data.msg)
          })
        }
      },
      saveData(data) {
        let _this = this
        let {isLogin, tokenValue} = data
        _this.setToken(tokenValue)
        _this.setIsLogin(isLogin)
      }
    }
  }
</script>
<style lang="less" scoped>
  .login-box{
    height: 100vh;
    overflow: auto;
    background: #f0f2f5 url('./bg.svg') no-repeat center 110px;
    background-size: 100%;
    padding-top: 200px;
    box-sizing: border-box;
    .login-container{
      width: 50%;
      min-width: 450px;
      max-width: 550px;
      margin: 0 auto;
    }
  }
</style>
